/* Elements comuns */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f5f5dc;
}

/* Menu superior */
#barra_navegacio {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333;
  color: white;
  padding: 12px 24px;
  z-index: 10;
}

#barra_navegacio .menu_dins {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

#barra_navegacio #logo_menu {
  max-width: 40px;
  margin-right: 10px;
}

#barra_navegacio #titol_general {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

#barra_navegacio #titol_general a {
  color: white;
  text-decoration: none;
}

#barra_navegacio ul {
  list-style: none;
  display: flex;
  margin-left: auto;
}

#barra_navegacio ul li {
  margin-left: 20px;
}

#barra_navegacio ul li a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  font-size: 1rem;
}

#barra_navegacio ul li a:hover {
  text-decoration: underline;
}

/* Estils index.html */
.contingut_index {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-top: 150px;
  width: 1200px;
}

.foto_portada-img {
  width: 100%;
  height: 100%;
  object-fit: cover; 
}

.boto_portada {
  position: absolute;
  bottom: -180px; 
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5); 
  padding: 10px;
}

/* Estils index.html */
section h2 {
  margin-top: 50px;
  font-size: 1.5rem;
  font-weight: 600;
}

section p {
  font-size: 1rem;
  line-height: 1.6;
  margin-top: 20px;
}


.contingut_index {
  height: auto;
}

.foto_portada-img {
  width: 480px;
  height: auto;
  margin-top: 135px;
}

.hero-title {
  font-size: 2rem;
  padding: 30px;
  position: relative;
  top: 0px;
}

#barra_navegacio ul {
  display: block;
  text-align: center;
}

#barra_navegacio ul li {
  margin: 15px 0;
}

